.projects-container {
  display: flex; /* Flexbox for horizontal alignment */
  flex-direction: row; /* Horizontal layout */
  flex-wrap: wrap; /* Allow wrapping to the next line if necessary */
  justify-content: center; /* Center the cards */
  gap: 35px; /* Space between cards */
  padding: 20px;
}

#projects .inner {
  padding-top: 0;
}

.project-card {
  background-color: #1c1c1c;
  border-radius: 10px;
  border: 1px solid #ffffff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 300px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.4s, box-shadow 0.2s;
}
.project-card:hover {
  transform: scale(1.15);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.project-icon {
  font-size: 40px;
  margin-bottom: 10px;
}

.project-title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
  color:#fff;
  cursor:default;
}

.project-description {
  font-size: 14px;
  color: #9f9f9f;
  margin-bottom: 20px;
  text-align: center;
  cursor:default;
}

.learn-more-button {
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
  border:none;
}

.learn-more-button:hover {
  color: #FF4C4C;
}
.project-image {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 15px;
  max-height: 150px;
}
@media screen and (max-width: 768px) {
  .projects-container {
    gap: 20px;
  }
  .project-card {
    max-width: 250px;
  }
  .project-icon {
    font-size: 30px;
  }
  .project-title {
    font-size: 16px;
  }
  .project-description {
    font-size: 12px;
  }
}


#projects .inner {
  padding: 100px 0 ;
}




/* Individual Project Pages css */

.project-detail{
  display: flex;
  flex-direction: column;
  align-items: center;
  height:100%;
  gap:20px;
}
.project-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;
  background-color: #1c1c1c;
  width:60%;
  border: 1px solid #ffffff;
  border-radius: 10px;
  height:100%;
}
.title-bar{
  display:flex;
  justify-content: space-between;
  flex-direction: row;
}
.title-image{
  max-height:64px;
  max-width:64px;
}

.button {
  margin-top:20px;
  transition:transform 0.2s;
}
.button:hover{
  transform: scale(1.1);
}